* {
  font-family: "Poiret One", cursive;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.contenido {
  padding: 10px 15px;
  width: 100%;
}
@media (min-width: 480px) {
  .contenido {
    padding: 10px 60px;
  }
}

h1 {
  font-weight: 700;
  font-size: 2rem;
}

p {
  font-family: "Poiret One", cursive;
  font-weight: bold;
  font-size: 1.1rem;
  letter-spacing: 1.5px;
}

::-webkit-scrollbar {
  width: 8px;
  /* Tama単o del scroll en vertical */
  height: 8px;
  /* Tama単o del scroll en horizontal */
}

::-webkit-scrollbar-thumb {
  background: #2874A6;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #2874A6;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

.progreso {
  width: 0%;
  height: 6px;
  background-color: #2874A6;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1002;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 30px;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.5s;
}
@media (min-width: 768px) {
  header {
    padding: 25px 60px;
  }
}
header .logo a {
  color: #FFFFFF;
  font-size: 20px;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 700;
  letter-spacing: 2px;
}
@media (min-width: 768px) {
  header .logo a {
    font-size: 24px;
  }
}
header nav ul {
  position: relative;
  display: flex;
}
@media (max-width: 768px) {
  header nav.menu-main {
    position: fixed;
    top: 0px;
    left: -100%;
    width: 100%;
    height: 100vh;
    background-color: white;
    transition: 0.5s;
  }
}
@media (max-width: 768px) {
  header nav.menu-main ul {
    display: block;
    margin-top: 40%;
    line-height: 65px;
    text-align: center;
  }
}
header nav.menu-main ul li {
  list-style: none;
  margin: 0 20px;
}
header nav.menu-main ul li a {
  color: #2874A6;
  font-size: 1em;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 3px;
  transition: all 0.5s ease;
}
@media (min-width: 768px) {
  header nav.menu-main ul li a {
    color: #FFFFFF;
    font-size: 0.8em;
  }
}
header nav.menu-main ul li a:hover {
  color: #2874A6;
}
@media (max-width: 768px) {
  header nav.activo {
    position: fixed;
    top: 0px;
    left: 0%;
    width: 100%;
    height: 100vh;
    background-color: white;
    transition: 0.5s;
  }
}
header .menutoggle {
  display: block;
  text-align: center;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  padding: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 10px;
  right: 5%;
}
@media (min-width: 768px) {
  header .menutoggle {
    display: none;
  }
}
header .menutoggle i {
  line-height: 30px;
  color: #2874A6;
  font-size: 1.5em;
  vertical-align: middle;
}
header a.a-btn {
  color: #2874A6 !important;
  border: 2px solid #2874A6;
  padding: 10px 20px;
  transition: all 0.8s ease;
}
header a.a-btn:hover {
  color: #FFFFFF !important;
  background-color: #2874A6;
}

.scroleado {
  background-color: #FFFFFF;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}
.scroleado nav.menu-main ul li a {
  color: #2874A6;
  font-size: 1rem;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 3px;
  transition: all 0.5s ease;
  font-family: "Poppins", sans-serif;
}
@media (min-width: 768px) {
  .scroleado nav.menu-main ul li a {
    font-size: 0.8rem;
  }
}
.scroleado .logo a {
  color: #2874A6;
}

.banner .wave {
  position: absolute;
  bottom: 0;
  width: 100%;
}
@media (max-width: 768px) {
  .banner .wave {
    height: 50px !important;
  }
}

.fondo {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url(../img/portada-principal.jpeg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.fondo .contenedor {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
}
.fondo .contenedor .presentacion {
  padding: 10px 30px;
  width: 100%;
  position: absolute;
  top: 13%;
  max-width: 50rem;
}
@media (min-width: 768px) {
  .fondo .contenedor .presentacion {
    padding: 10px 60px;
    top: 17%;
  }
}
.fondo .contenedor .presentacion h1, .fondo .contenedor .presentacion h2, .fondo .contenedor .presentacion p {
  color: white;
  margin: 0;
  padding: 0;
  line-height: 3.5rem;
}
@media (min-width: 768px) {
  .fondo .contenedor .presentacion h1, .fondo .contenedor .presentacion h2, .fondo .contenedor .presentacion p {
    line-height: 5.5rem;
  }
}
.fondo .contenedor .presentacion h1 {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .fondo .contenedor .presentacion h1 {
    font-size: 3.5rem;
  }
}
.fondo .contenedor .presentacion h1 span {
  display: inline-block;
}
.fondo .contenedor .presentacion h1 span::after {
  content: "";
  height: 1rem;
  width: 100%;
  background-color: #2874A6;
  display: block;
  margin: -1.7rem 0 0 0;
}
.fondo .contenedor .presentacion h2 {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .fondo .contenedor .presentacion h2 {
    font-size: 3rem;
  }
}
.fondo .contenedor .presentacion p {
  color: #FFFFFF !important;
  font-size: 1em;
  margin-top: 10px;
  font-family: "Poiret One", cursive;
}
@media (min-width: 768px) {
  .fondo .contenedor .presentacion p {
    font-size: 2em;
  }
}
.fondo .contenedor .presentacion a {
  position: relative;
  color: #2874A6;
  padding: 10px 30px;
  text-decoration: none;
  border: 2px solid #2874A6;
  display: inline-block;
  margin-top: 20px;
  font-size: 1.5em;
  transition: all 0.8s ease;
  overflow: hidden;
}
.fondo .contenedor .presentacion a:hover {
  color: white;
  background-color: #2874A6;
}

.sobre-mi {
  margin-top: 0;
